
<script setup>

  import { onMounted, onUnmounted,reactive, ref, toRefs } from 'vue';
  import { useLocaler, useLocale } from 'vue-localer';
  import { Icon } from '@iconify/vue';
  import VideoPlayer from '@/components/element/VideoPlayer.vue'
  import Slider from '@vueform/slider'
  import Hero from '@/components/element/Hero.vue'
  import Loading from '@/components/element/Loading.vue';
  import { BigNumber, ethers } from "ethers";
  import { popup,toast,confirm,claimMysteryDialog,verifyMysteryDialog,whitelistMysteryDialog } from '@/libraries/dialog.js'
  import { mapState, mapGetters, mapMutations, mapActions } from '@/core/store.js'
  import eventBus from '@/libraries/eventBus.js';
  import axios from "axios";

  //@vueform/slider/themes/default.css
  const language = useLocale();
  const { account } = mapState('wallet');
  const { apiHost,heroHost,analysis } = mapState('game');
  const heroes = reactive({
    data:{
      pageIndex: 0,
      pageCount: 0,
      pageSize: 0,
      totalCount: 0,
      items: []
    },
    loading:false,
    turnPage: (page)=>{
      filter.pageIndex = page;
      heroes.getList();
    },
    goToTop : ()=>{
      window.scrollTo(0,0);
    },
    getList: ()=>{

      if(heroes.loading==true){
        return;
      }

      heroes.loading = true;

      let parameters = { 
        id:parseInt(filter.id)?parseInt(filter.id):0,
        sort:filter.sort,
        rarity:filter.rarity,
        owner:filter.owner,
        gender:filter.male && filter.female?0:filter.male?1:filter.female?2:0,
        pageIndex:filter.pageIndex,
        fire:filter.fire,
        ice:filter.ice,
        earth:filter.earth,
        poison:filter.poison,
        electric:filter.electric,
        metal:filter.metal,
        strength:filter.strength,
        intellect:filter.intellect,
        spirit:filter.spirit,
        stamina:filter.stamina,
        agility:filter.agility,
        luck:filter.luck,
      };
      let url = apiHost.value+"/hero";
        axios.post(url,parameters)
        .then(r => {
          if(r.data && r.data.data){
            heroes.data = r.data.data;
          }else{
            heroes.data = {
              pageIndex: 0,
              pageCount: 0,
              pageSize: 0,
              totalCount: 0,
              items: []
            };
          }
        })
        .catch(e => {
          console.log(e);
        })
        .finally(() => {
          heroes.loading = false;
        });
    },
    getRarityName: (rarity)=>{
        if(rarity == 1){
            return language.value.mystery.table.rarity.standard;
        }else if(rarity == 2){
            return language.value.mystery.table.rarity.execllent;
        }else if(rarity == 3){
            return language.value.mystery.table.rarity.rary;
        }else if(rarity == 4){
            return language.value.mystery.table.rarity.epic;
        }else if(rarity == 5){
            return language.value.mystery.table.rarity.miracle;
        }else if(rarity == 6){
            return language.value.mystery.table.rarity.legendary;
        }
    },
    getRarityColor: (rarity)=>{
        if(rarity == 1){
            return "bg-gray-700";
        }else if(rarity == 2){
            return "bg-green-800";
        }else if(rarity == 3){
            return "bg-blue-800";
        }else if(rarity == 4){
            return "bg-purple-800";
        }else if(rarity == 5){
            return "bg-orange-800";
        }else if(rarity == 6){
            return "bg-red-800";
        }
    }, 
    getRoleName: (role) =>{
        if(role == 1){
            return language.value.mystery.role.warrior;
        }else if(role == 2){
            return language.value.mystery.role.mage;
        }else if(role == 3){
            return language.value.mystery.role.hunter;
        }else if(role == 4){
            return language.value.mystery.role.sniper;
        }else if(role == 5){
            return language.value.mystery.role.boxer;
        }else if(role == 6){
            return language.value.mystery.role.monk;
        }
    },
    currency: (value)=>{
      return (+value || 0).toFixed(0).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,')
    }
  });

  const filter = reactive({
    id:0,
    sort:0,
    owner:"",
    male:false,
    female:false,
    rarity:0,
    pageIndex:0,
    counter:0,
    analysis: () =>{
      let count = 0;
      count+=filter.id>0?1:0;
      count+=filter.sort>0?1:0;
      count+=filter.male?1:0;
      count+=filter.female?1:0;
      count+=filter.rarity>0?1:0;
      count+=filter.owner.length>0?1:0;
      count+=filter.fire[0]>0 && filter.fire[1]>0?1:0;
      count+=filter.ice[0]>0 && filter.ice[1]>0?1:0;
      count+=filter.earth[0]>0 && filter.earth[1]>0?1:0;
      count+=filter.poison[0]>0 && filter.poison[1]>0?1:0;
      count+=filter.electric[0]>0 && filter.electric[1]>0?1:0;
      count+=filter.metal[0]>0 && filter.metal[1]>0?1:0;

      count+=filter.strength[0]>0 && filter.strength[1]>0?1:0;
      count+=filter.intellect[0]>0 && filter.intellect[1]>0?1:0;
      count+=filter.spirit[0]>0 && filter.spirit[1]>0?1:0;
      count+=filter.stamina[0]>0 && filter.stamina[1]>0?1:0;
      count+=filter.agility[0]>0 && filter.agility[1]>0?1:0;
      count+=filter.luck[0]>0 && filter.luck[1]>0?1:0;
      

      filter.counter = count;
    },
    clean: () =>{
      filter.id=0;
      filter.sort=0;
      filter.male=false;
      filter.female=false;
      filter.rarity=0;
      filter.pageIndex=0;
      filter.owner="";

      filter.fire=[0, 100];
      filter.ice=[0,100];
      filter.earth=[0,100];
      filter.poison=[0,100];
      filter.electric=[0, 100];
      filter.metal=[0,100];

      filter.strength=[0,100];
      filter.intellect=[0,100];
      filter.spirit=[0,100];
      filter.stamina=[0,100];
      filter.agility=[0,100];
      filter.luck=[0,100];

      filter.counter = 0;
      heroes.getList();
    },
    onIdChange: (event) =>{
      filter.id = event.target.value;
      filter.analysis();
      heroes.getList();
    },
    onSortChange: (event) =>{
      filter.sort = parseInt(event.target.value);
      filter.analysis();
      heroes.getList();
    },
    onMyHeroChange: ()=>{
      if(filter.owner.length<=0){
        filter.owner = account.value;
      }else{
        filter.owner = "";
      }
      console.log(filter.owner);
      filter.analysis();
      heroes.getList();
    },
    onRarityChange: (value) =>{
      if(filter.rarity!=value){
        filter.rarity = value;
      }else{
        filter.rarity = 0;
      }
      filter.analysis();
      heroes.getList();
    },
    onGenderChange: (event) =>{
      let gender = filter.male && filter.female?0:filter.male?1:filter.female?2:0;
      filter.analysis();
      heroes.getList();
    },
    fire:[0, 100],
    ice:[0,100],
    earth:[0,100],
    poison:[0,100],
    electric:[0, 100],
    metal:[0,100],
    onElementChange: (value) =>{
      filter.analysis();
      heroes.getList();
    },
    strength:[0,100],
    intellect:[0,100],
    spirit:[0,100],
    stamina:[0,100],
    agility:[0,100],
    luck:[0,100],
    onAttributeChange: (value) =>{
      filter.analysis();
      heroes.getList();
    },
  });

  onMounted(() => {
     heroes.getList();
  });

  onUnmounted(()=>{
    
  });

</script>

